import { Component, OnInit } from '@angular/core';
import { MatDialogConfig, MatDialog, MatDialogRef } from '@angular/material';
import { Router } from '@angular/router';

import { Action } from '../shared/model/action';
import { Event } from '../shared/model/event';
import { Message } from '../shared/model/message';
import { User } from '../shared/model/user';
import { SocketService } from '../shared/services/socket.service';
import { UsernameComponent } from '../username/username.component';


@Component({
    selector: 'nc-home',
    templateUrl: './home.component.html',
    styleUrls: ['./home.component.css']
})

export class HomeComponent implements OnInit {

    action = Action;
    user: User;
    dialogRef: MatDialogRef<UsernameComponent>;
    messages: Message[] = [];
    messageContent: string;
    ioConnection: any;

    readonly AVATAR_URL: string = 'https://api.adorable.io/avatars/285/';

    constructor(private socketService: SocketService, public dialog: MatDialog,
        private router: Router) { }

    ngOnInit(): void {

    }

    private openUserPopup(): void {
        const dialogConfig = new MatDialogConfig();

        dialogConfig.disableClose = true;
        dialogConfig.autoFocus = true;
        dialogConfig.data = {};

        this.dialogRef = this.dialog.open(UsernameComponent, dialogConfig);

        this.dialogRef.afterClosed().subscribe(paramsDialog => {
            if (!paramsDialog) {
                return;
            }

            this.initModel(paramsDialog.username);

            this.initIoConnection();
            this.sendNotification(paramsDialog, Action.JOINED);

            this.router.navigateByUrl('/nuage/share');
        });
    }

    private initIoConnection(): void {
        this.socketService.initSocket();

        this.ioConnection = this.socketService.onMessage()
            .subscribe((message: Message) => {
                this.messages.push(message);
            });

        this.socketService.onEvent(Event.CONNECT)
            .subscribe(() => {
                console.log('connected');
            });

        this.socketService.onEvent(Event.DISCONNECT)
            .subscribe(() => {
                console.log('disconnected');
            });
    }

    private initModel(username: string): void {
        this.user = <User> {
            id: username,
            avatar: this.AVATAR_URL + username
        };
    }

    public sendNotification(params: any, action: Action): void {
        this.socketService.send({
            from: this.user,
            action: Action.JOINED
        });
    }

}
